import { autocompletion, Completion, CompletionContext, CompletionResult, CompletionSource } from "@codemirror/autocomplete";
import { getExpressionContext } from "../expression";
import { compact, get } from "lodash-es";

const generateMustachCompletionOptions = (prefix: string, data: any = {}): Completion[] => {
  let result: Completion[] = [];
  if (!prefix.includes('.')) {
    result = Object.keys(data).map(key => {
      const value = data[key];
      return {
        label: `${key}.`,
      }
    });
  } else {
    const lastPeriod = prefix.lastIndexOf('.');
    const path = prefix.substring(0, lastPeriod);
    const field = prefix.endsWith('.') ? '' :  prefix.substring(lastPeriod + 1);
    const pathValue = get(data, path);
    if (typeof pathValue === 'object' && !(field in pathValue)) {
      result = compact(Reflect.ownKeys(pathValue).map((key) => {
        if (typeof key !== 'string') {
          return;
        }
        const value = pathValue[key];
        return {
          label: `${path}.${key}`,
          displayLabel: key,
        }
      }));
    }
  }
  
  return result.filter(completion => completion.label.startsWith(prefix));
}

const mustacheCompletion: CompletionSource = (context) => {
  // {{}}
  const text = context.state.doc.toString()
  if (0 === text.length && 0 === context.pos) {
    return {
      from: 0,
      options: [
        {
          label: '{{  }}',
          apply(view, completion, from, to) {
            console.log(from)

            view.dispatch({
              changes: { from, to, insert: '{{  }}' },
              selection: {
                anchor: from + 3,
              }
            })
          },
        }
      ]
    }
  }

  // 
  const textBefore: string = context.state.sliceDoc(0, context.pos);
  const textAfter: string = context.state.sliceDoc(context.pos); 

  //  {{ }} 
  const lastOpen = textBefore.lastIndexOf("{{");
  const nextClose = textAfter.indexOf("}}");
  
  //  {{}}  null
  if (lastOpen === -1 || nextClose === -1) return null;
  
  //  {{  }} 
  if (context.pos < lastOpen + 2 || context.pos > context.pos + nextClose) {
    return null;
  }

  const prefixPos = Math.max(lastOpen + 2, textBefore.lastIndexOf(' ')+ 1);
  const prefix = textBefore.substring(prefixPos);
  const options = generateMustachCompletionOptions(prefix, getExpressionContext());

  //  {{}} 
  return {
    from: prefixPos,
    to: context.pos,
    // validFor: /^\w*$/,
    options,
  };
}

export const mustacheAutocomplete = autocompletion({
  override: [mustacheCompletion]
});