:root[data-theme="light"]
{
  --canvas-gridline: #ccc;
  --container-bg-0: rgba(25, 118, 210, 0.87);
  --container-bg-1: #1976d2;
  --container-bg-2: #fff;
  --container-bg-3: #f6f6f6;
  --container-bg-hover-1: rgba(25, 118, 210, .20);
  --border-1: rgba(0, 0, 0, 0.23);
  --border-focus-primary: #1976d2;
  --border-hover-1: rgba(0, 0, 0, 0.87);
  --cursor-1: #000000;
  --text-input-0: #ffffff;
  --text-input-1: #000000;
  --text-input-2: rgba(0, 0, 0, 0.85);
}

:root[data-theme="dark"]{
  --canvas-gridline: #555555;
  --container-bg-0: rgba(25, 118, 210, 0.87);
  --container-bg-1: #222222;
  --container-bg-2: #323232;
  --container-bg-3: #090909;
  --container-bg-hover-1: rgba(25, 118, 210, .20);
  --border-1: rgba(255, 255, 255, 0.23);
  --border-focus-primary: #90caf9;
  --border-hover-1: #ffffff;
  --cursor-1: #ffffff;
  --text-input-0: #ffffff;
  --text-input-1: #ffffff;
  --text-input-2: rgba(255, 255, 255, 0.85);
}

.src-lowCode-index__low-code-container {
  display: flex;
  padding-top: 10px;

  --text-1: #000;
  --text-2: #555555;
}

.src-lowCode-index__canvas-container {
  height: calc(100vh - 80px);
  border: 1px solid #cdcdcd;
  width: 80vw;
  position: relative;
  overflow: auto;
}

body {
  padding: 0;
  margin: 0;
}

.src-lowCode-index__previewer-canvas-container {
  height: calc(100vh - 80px);
}

.src-lowCode-index__components-panel-container {
  width: 100px;
}

