import React from "react";
import { DndProvider } from 'react-dnd';
import { HTML5Backend } from 'react-dnd-html5-backend';
import { BrowserRouter, Routes, Route, useLocation } from "react-router";
import Editor from "./editor";
import Previewer from "./previewer";
import { StrictMode } from "react";
import { AppBar, Toolbar, IconButton, Typography, Drawer, Box, List, ListItem, ListItemButton, ListItemIcon, ListItemText, ThemeProvider, CssBaseline } from "@mui/material";
import MenuIcon from '@mui/icons-material/Menu';
import PlayIcon from '@mui/icons-material/PlayArrow';
import DownloadIcon from '@mui/icons-material/Download'
import UploadIcon from '@mui/icons-material/Upload';
import EditorIcon from '@mui/icons-material/Construction';
import { SnackbarProvider } from 'notistack';
import { exportSchema, preview, uploadSchema } from "./misc";
import Tooltip from "./builtIn/components/Tooltip";
import { useState } from "react";
import useGlobalStore from "./globalStore";
import { themeConfig } from "./theme";
import { useEffect } from "react";

const TopMenu = ({ closeWrapper }) => {
  return (
    <Box
      sx={{ width: 250 }}
      role="presentation"
    >
      <List disablePadding>
        <ListItem disablePadding>
          <ListItemButton
            onClick={() => {
              closeWrapper?.();
            }}
          >
            <ListItemIcon>
              <EditorIcon />
            </ListItemIcon>
            <ListItemText>
              
            </ListItemText>
          </ListItemButton>
        </ListItem>
      </List>
    </Box>
  )
}

const Platform = (props) => {

  const { children } = props;

  const location = useLocation();

  const [drawerOpen, setDrawerOpen] = useState(false);
  const theme = useGlobalStore(state => state.theme);
  const toggleTheme = useGlobalStore(state => state.toggleTheme);
  const selectedTheme = themeConfig[theme];

  useEffect(() => {
    document.documentElement.setAttribute('data-theme', theme);
    localStorage.setItem('theme', theme);
  }, [theme]);

  return (
    <SnackbarProvider maxSnack={3} autoHideDuration={1000}>
      <ThemeProvider theme={selectedTheme.theme}>
        <CssBaseline />
        <Drawer
          open={drawerOpen}
          onClose={() => {
            setDrawerOpen(false);
          }}
        >
          <TopMenu
            closeWrapper={() => setDrawerOpen(false)}
          />
        </Drawer>
        <AppBar position="static">
          <Toolbar>
            <IconButton
              size="large"
              edge="start"
              color="inherit"
              aria-label="menu"
              sx={{ mr: 2 }}
              onClick={() => {
                setDrawerOpen(true);
              }}
            >
              <MenuIcon />
            </IconButton>
            <Typography variant="h6" component="div" sx={{ flexGrow: 1 }}>
              LOW_CODE
            </Typography>
            <Tooltip
              title=''
              placement="bottom"
              arrow
              offset={[0, -10]}
            >
              <IconButton
                color="inherit"
                size="large"
                onClick={uploadSchema}
              >
                <UploadIcon />
              </IconButton>
            </Tooltip>
            <Tooltip
              title=''
              placement="bottom"
              arrow
              offset={[0, -10]}
            >
              <IconButton
                color="inherit"
                size="large"
                onClick={exportSchema}
              >
                <DownloadIcon />
              </IconButton>
            </Tooltip>
            {location.pathname !== '/preview' && <Tooltip
              title=''
              placement="bottom"
              arrow
              offset={[0, -10]}
            >
              <IconButton
                color="inherit"
                size="large"
                onClick={preview}
              >
                <PlayIcon />
              </IconButton>
            </Tooltip>}
            <Tooltip
              title=''
              placement="bottom"
              arrow
              offset={[0, -10]}
            >
              <IconButton
                color="inherit"
                size="large"
                onClick={toggleTheme}
              >
                {selectedTheme.icon}
              </IconButton>
            </Tooltip>
          </Toolbar>
        </AppBar>
        <div>
          {children}
        </div>
      </ThemeProvider>
    </SnackbarProvider>
  )
}

const LowCode = () => {

  return (
    <StrictMode>
      <BrowserRouter>
        <Platform>
          <Routes>
            <Route index element={<Editor />} />
            <Route path="preview" element={<Previewer />} />
          </Routes>
        </Platform>
      </BrowserRouter>
    </StrictMode>
  )
}

export default () => {
  return <DndProvider backend={HTML5Backend}><LowCode /></DndProvider>;
} 